@import url('https://fonts.googleapis.com/css?family=Lobster');
@import url('https://fonts.googleapis.com/css?family=Roboto');

@media (max-width: 700px) {
  #nav-container {
    background-color: white;
    opacity: 0.6;
  }

  .logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: -25px;
  }

  #timeContainer {
    color: white;
    font-size: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}

@media(min-width: 701px) {
  .logo {
    display: none
  }

  #timeContainer {
    color: white;
    font-size: 48px;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
  }
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: white;
}

.body2{
  background-image: url(/styles/back3.jpg);
  background-size: cover;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
}

#nav-container {
  height: 25%;
  width:100%;
  font-size: 34px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
}

.logo-container a {
  text-decoration: none;
  color: white;
  margin-left: 20px;
  opacity: 0.9;
  font-family: 'Lobster', cursive;
}

.mainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 75vh;
  width: 100%;
  padding: 20px;
  margin-top: 100px;
  font-family: 'Lobster', cursive;
  font-size: 48px;
}

.timeContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  color: black;
  text-align: center;
  flex-wrap: wrap;
}

#inputBox {
  width: 75%;
  height: 90%;
  border-radius: 5px;
  padding-left: 7px;
  opacity: 0.9;
  outline: none;
  font-size: 18px;
}

#button {
  margin-left: 5px;
  padding-top: 3px;
  margin-top: -15px;
}

.bookSearchContainer {
  display: flex;
  justify-content: center;
  align-self: center;
  width: 33%;
  height: 5vh;
  margin-top: 75px;
}

.results-container {
  display: flex;
  justify-content: center;
  color: white;
}

.results a{
  color: white;
  text-decoration: none;
  font-size: 18px;
  padding: 3px;
}


/*details page*/
.header-container {
  background-image: url(/styles/header.jpg);
  background-size: cover;
  width: 100%;
  height: 25vh;
}

.details {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
  padding: 20px;
}

.desc {
  text-align: center;
}

.ui-button {
  height: 25px;
  width: 125px;
  border-radius: 5px;
}

.bookImage {
  border-radius: 1px;
}
